import React, { useState, useEffect } from 'react'
import { Form, Row, Col, Button, Select, Input, DatePicker, Tooltip } from 'antd';
import Api from '../../helpers/api';
import moment from 'moment';

import {
    BarsOutlined,
} from '@ant-design/icons';
import handleApiError from '../../helpers/handleApiError';
import NoteModal from '../../components/Modal/NoteModal';

const api = new Api();
const { Option } = Select;

const FormReservation = (props) => {

    const [errors, setErrors] = useState({})
    const [form] = Form.useForm();
    const [inputDisabled, setInputDisabled] = useState(false)

    const [formData, setFormData] = useState({
        code: `RES/${moment(new Date()).format("YYMMDD/HHmmss")}`,
        type: '',
        plant_id: '',
        plant_sender: '',
        room_id: '',
        room_sender: '',
        note: '',
        receiver: '',
        sender: '',
    })

    const [select, setSelect] = useState({
        plant: [],
        plant_sender: [],
        room: [],
        room_sender: [],
        vendor: [],
    })

    const [showModal, setShowModal] = useState({
        modalNote: false,
    })

    useEffect(() => {
        fetchPlant()
    }, []);

    useEffect(() => {
        if (formData.type === 0) {
            fetchVendor()
        }

        if (formData.plant_id) {
            fetchRoom('room', formData.plant_id)
        }

        if (formData.plant_sender) {
            fetchRoom('room_sender', formData.plant_sender)
        }
    }, [formData.type, formData.plant_sender, formData.plant_id]);

    useEffect(() => {
        const { view, process } = props

        if (view || process) {
            setInputDisabled(true)
            setFormData(props.formData)
            form.setFieldsValue(props.formData);
        }
    }, [props.formData]);

    const fetchPlant = (name, q) => {
        api.getPlant({ q: q }).then(response => {
            const { data } = response.data;
            let newSelect = { ...select };

            if (name) {
                newSelect[name] = data;
                setSelect(newSelect)
            } else {
                newSelect.plant = data;
                newSelect.plant_sender = data;
                setSelect(newSelect)
            }

        }).catch(err => {
            console.log(err)
            // handleApiError(err, props)
        })
    };

    const fetchVendor = (q) => {
        api.getVendor({ q: q }).then(response => {
            const { data } = response.data;
            setSelect({ ...select, ['vendor']: data })
        }).catch(err => {
            console.log(err)
            // handleApiError(err, props)
        })
    };

    const fetchRoom = (name, value, q) => {
        api.getRoom({ plant_id: [value], q })
            .then(response => {
                const { data } = response.data;

                const newSelect = { ...select };
                newSelect[name] = data;
                setSelect(newSelect)
            }).catch(err => {
                console.log(err)
                // handleApiError(err, props)
            })
    };

    const handleShowModal = (name, value) => {
        setShowModal({ ...showModal, [name]: value })
    }

    return (
        <Form name="complex-form" labelCol={{ span: 8 }} wrapperCol={{ span: 24 }}
            form={form}>
            <Row>
                <Col md={12} >
                    <Form.Item name="code" label="Code"
                        validateStatus={errors && errors.code ? 'error' : undefined}
                        help={errors ? errors.code : null}
                        rules={[{ required: true, message: 'Please input code' }]}>
                        <Input disabled={true} />
                    </Form.Item>

                    <Form.Item name="type" label="Type"
                        validateStatus={errors && errors.type ? 'error' : undefined}
                        help={errors ? errors.type : null}
                        rules={[{ required: !inputDisabled, message: 'Please select type' }]}>
                        <Select
                            disabled={inputDisabled}
                        >
                            <Option value={0}>Request to Vendor</Option>
                            <Option value={1}>Transfer with Do</Option>
                            <Option value={2}>Transfer without Do</Option>
                        </Select>
                    </Form.Item>

                    <Form.Item colon={false} label="Receiver" style={{ fontWeight: "bold" }} />
                    <Form.Item name="plant_id" label="Plant"
                        validateStatus={errors && errors.plant_id ? 'error' : undefined}
                        help={errors ? errors.plant_id : null}
                        rules={[{ required: !inputDisabled, message: 'Please input plant' }]}>
                        <Select
                            disabled={inputDisabled}>
                            {select.plant.map(item => (
                                <Option key={item.id} value={item.id}>{item.name}</Option>
                            ))}
                        </Select>
                    </Form.Item>

                    <Form.Item name="room_id" label="Room"
                        validateStatus={errors && errors.room_id ? 'error' : undefined}
                        help={errors ? errors.room_id : null}
                        rules={[{ required: !inputDisabled, message: 'Please input room' }]}>
                        <Select
                            disabled={inputDisabled}>
                            {select.room.map(item => (
                                <Option key={item.id} value={item.id}>{item.name}</Option>
                            ))}
                        </Select>
                    </Form.Item>

                    <Form.Item label="Responsible Person">
                        {formData.receiver}
                    </Form.Item>
                </Col>
                <Col md={12}>

                    <Form.Item colon={false} label="Sender" style={{ fontWeight: "bold" }} />

                    {formData.type !== ''
                        ?
                        formData.type === 0
                            ?
                            <Form.Item name="vendor_id" label="Vendor"
                                validateStatus={errors && errors.vendor_id ? 'error' : undefined}
                                help={errors ? errors.vendor_id : null}
                                rules={[{ required: !inputDisabled, message: 'Please input plant' }]}>
                                <Select
                                    disabled={inputDisabled}>
                                    {select.vendor.map(item => (
                                        <Option key={item.id} value={item.id}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Form.Item>
                            :
                            <div>
                                <Form.Item name="plant_sender" label="Plant"
                                    validateStatus={errors && errors.plant_sender ? 'error' : undefined}
                                    help={errors ? errors.plant_sender : null}
                                    rules={[{ required: !inputDisabled, message: 'Please input plant' }]}>
                                    <Select
                                        disabled={inputDisabled}>
                                        {select.plant_sender.map(item => (
                                            <Option key={item.id} value={item.id}>{item.name}</Option>
                                        ))}
                                    </Select>
                                </Form.Item>

                                <Form.Item name="room_sender" label="Room"
                                    validateStatus={errors && errors.room_sender ? 'error' : undefined}
                                    help={errors ? errors.room_sender : null}
                                    rules={[{ required: !inputDisabled, message: 'Please input room' }]}>
                                    <Select
                                        disabled={inputDisabled}>
                                        {select.room_sender.map(item => (
                                            <Option key={item.id} value={item.id}>{item.name}</Option>
                                        ))}
                                    </Select>
                                </Form.Item>

                                <Form.Item label="Responsible Person">
                                    {formData.sender}
                                </Form.Item>
                            </div>
                        : null
                    }

                    <Form.Item name="delivery_date" label="Delivery Date"
                        validateStatus={errors && errors.room_id ? 'error' : undefined}
                        help={errors ? errors.room_id : null}
                        rules={[{ required: !inputDisabled, message: 'Please input room' }]}>
                        <DatePicker style={{ width: '100%' }}
                            disabled={inputDisabled} />
                    </Form.Item>

                    <Form.Item label="Note"
                        validateStatus={errors && errors.note ? 'error' : undefined}
                        help={errors ? errors.note : null}
                        rules={[{ required: false, message: 'Please input note' }]}>
                        <Tooltip title="Note">
                            <Button icon={<BarsOutlined />}
                                onClick={() => handleShowModal('modalNote', true)}
                            />
                        </Tooltip>
                        <NoteModal
                            showNoteModal={showModal.modalNote}
                            handleShowModal={handleShowModal}
                            data={formData.note}
                            modalName='modalNote'
                            formName='note'
                            updateAction={false}
                        />
                    </Form.Item>
                </Col>
            </Row>
        </Form >
    )
}

export default FormReservation;