import React, { useState, useEffect } from 'react'
import { InputNumber, Table, Input } from 'antd';

const TableMaterialVendor = (props) => {

    const [quantity, setQuantity] = useState([])
    const [price, setPrice] = useState([])
    const [selectedRowKeys, setSelectedRowKeys] = useState([])
    const [deliveryQuantity, setDeliveryQuantity] = useState([])
    const [deliverySerial, setDeliverySerial] = useState([])

    const { add } = props

    useEffect(() => {
        if (props.selectedMaterial.length > 0) {
            let newQuantity = [...quantity]
            let newPrice = [...price]
            props.selectedMaterial.map((item) => {
                newQuantity[item.id] = item.quantity
                setQuantity(newQuantity)

                newPrice[item.id] = item.price
                setPrice(newPrice)

                let deliveryQty = []
                props.selectedMaterial.map((item, index) => {
                    deliveryQty[index] = { id: item.id, val: item.delivery_quantity }
                })
                let newDeliveryQuantity = [...deliveryQuantity, ...deliveryQty]
                setDeliveryQuantity(newDeliveryQuantity)

                let serials = []
                props.selectedMaterial.map(item => {
                    if (item.do_detail.serial_numbers) {
                        item.do_detail.serial_numbers.map((value, key) => {
                            serials.push({ id: item.id, key, val: value.serial_number })
                        })
                    }
                })
                let newDeliverySerial = [...deliverySerial, ...serials]
                setDeliverySerial(newDeliverySerial)
            })
        }

    }, [props.selectedMaterial])

    //on check table item
    const onSelectChange = (selectedRowKeys) => {
        setSelectedRowKeys(selectedRowKeys)
        props.selectedMaterialDelete(selectedRowKeys)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const columns = [
        {
            title: 'Material Code',
            dataIndex: 'material_code',
            width: 200
        },
        {
            title: 'Quantity',
            dataIndex: 'id',
            render: (id, record) => {
                let qty = quantity[id] ? quantity[id] : 0
                let quantityUom = record.material.quantity_uom > 1 && ` (${record.material.quantity_uom * qty})`
                return <>
                    <InputNumber
                        min={0}
                        size='small'
                        readOnly={props.view || props.process}
                        value={qty} />

                    {quantityUom}
                </>
            },
            width: 100
        },
        {
            title: 'Delivery Quantity',
            dataIndex: 'id',
            render: (id, record) => {
                if (props.process || props.view) {
                    let maxQty = quantity[id] ? quantity[id] : 0
                    let index = deliveryQuantity.map(item => item.id).lastIndexOf(id)
                    let delivQty = deliveryQuantity[index] && deliveryQuantity[index].id === id
                        ? deliveryQuantity[index].val
                        : 0
                    let quantityUom = record.material.quantity_uom > 1 && ` (${record.material.quantity_uom * delivQty})`
                    return <>
                        <InputNumber
                            min={0}
                            max={maxQty}
                            size="small"
                            readOnly={props.view}
                            value={delivQty} />

                        {quantityUom}
                    </>
                }
            },
            width: 100
        },
        // {
        //     title: 'Serial Number',
        //     dataIndex: 'id',
        //     render: (id, record) => {
        //         if ((props.process || props.view) && record.material.serial_number == 1) {
        //             let indexStock = deliveryQuantity.map(item => item.id).lastIndexOf(id)
        //             let stock = deliveryQuantity[indexStock] ? deliveryQuantity[indexStock].val : 0
        //             let serialInput = [];
        //             for (let i = 0; i < parseInt(stock); i++) {
        //                 let indexSerial = deliverySerial.map(item => { if (item.id === id && item.key === i) { return item.id } }).lastIndexOf(id)
        //                 serialInput.push(
        //                     <div style={{ padding: 2 }} key={i}>
        //                         <Input
        //                             readOnly={props.view}
        //                             size="small"
        //                             style={{ width: '100%' }}
        //                             value={
        //                                 deliverySerial[indexSerial] && deliverySerial[indexSerial].id === id && deliverySerial[indexSerial].key === i
        //                                     ? deliverySerial[indexSerial].val
        //                                     : ''
        //                             } />
        //                     </div>
        //                 );
        //             }

        //             return serialInput
        //         }
        //     },
        //     width: 150,
        //     className: 'col-center',
        // },
        {
            title: 'Price (Rp)',
            dataIndex: 'id',
            render: (id) => {
                let valuePrice = price[id] ? price[id] : 0
                return <Input
                    min={0}
                    className='col-number'
                    size='small'
                    style={{ width: '70%' }}
                    readOnly={true}
                    value={`${valuePrice}`.replace(/\B(?=(\d{3})+(?!\d))/g, '.')}
                />
            },
            width: 200
        },
        {
            title: 'Subtotal (Rp)',
            dataIndex: 'id',
            render: (id) => {
                let qty = quantity[id] ? quantity[id] : 0
                let valuePrice = price[id] ? price[id] : 0
                let subtotal = parseInt(valuePrice) * parseInt(qty)

                return <Input
                    min={0}
                    className='col-number'
                    size='small'
                    style={{ width: '70%' }}
                    readOnly={true}
                    value={`${subtotal}`.replace(/\B(?=(\d{3})+(?!\d))/g, '.')}
                />
            },
            width: 200
        },
        {
            title: 'UoM',
            dataIndex: 'uom',
            render: (uom) => {
                if (uom) { return uom.name }
            },
            width: 100
        },
    ];
    return (
        <Table
            rowSelection={add && rowSelection}
            rowKey={record => record.id}
            dataSource={props.selectedMaterial}
            columns={columns}
            pagination={false}
            size='small'
            scroll={{ y: 300, x: 1000 }} />
    )
}

export default TableMaterialVendor;